export default {
	data() {
		return {
			currentTab: 0,
			tabClick: 0,
			navIndex: '',
			status: ['审核中', '进行中', '已完成'],
			notStart: '',
			start: '',
			end: '',
			userMsg: '',
			aimg: '',
			date: '',
			uplv: false,
			cid: '',
			videoList: [],
			picsList: [],
			height: '',
			width: ''
		}
	},
	watch: {

	},
	onLoad(option) {
		uni.loadFontFace({
			family: 'Alibaba PuHuiTi',
			source: 'url("https://www.mastervan.cn/static/project/dolphin/static/font/alifont/AlibabaPuHuiTi-Light.ttf")',
			success: function() {
				console.log('load font success')
			},
			fail() {
				//console.log(456)
			}
		})

	},
	onReady() {
		//用户信息
		let openId = uni.getStorageSync('openid');
		let that = this;
		that.$http.getUserMsgs({
			openId: openId
		}).then(res => {
			let h = res.data.data.partTimeMessage.height
			let w = res.data.data.partTimeMessage.weight
			that.height = h
			that.width = w
			that.$http.findDetailUserMsgs({
				openId: openId,
				userId: res.data.data.userId
			}).then(res => {
				// console.log("userdetailMsgs=", res)
				let picsList = res.data.data.photoUrlList
				let videoList = []
				videoList.push(res.data.data.videoUrl)
				if (res.data.data.dolphinVideoUrl != null) {
					videoList.push(res.data.data.dolphinVideoUrl)
				}
				if (res.data.data.dolphinPhotoList.length > 0) {
					picsList = picsList.concat(res.dada.data.dolphinPhotoList)
				}
				that.videoList = videoList
				console.log("videoList", that.videoList);
				that.picsList = picsList
			})
		})
		that.$http.partTimeMsgs({
			openId: openId
		}).then(res => {
			// console.log("userMsgs=", res)
			that.userMsg = res.data.data
			uni.setStorageSync('cid', res.data.data.cid)
			//websocket
			let cid = res.data.data.cid;
			that.cid = cid;
			let set = setInterval(function() {
				that.$http.findOrderList({
					cid: cid,
					openId: openId
				}).then(res => {
					var oldSt = JSON.stringify(res.data.data);
					var order = res.data.data;
					console.log("订单列表=", order);
					that.$http.findOrderID({
						openId: openId
					}).then(res => {
						console.log("dingOrder", res);
						let arr3 = res.data.data;
						let allOid = [];
						for (let i in arr3) {
							allOid.push(arr3[i].orderId)
						}
						// 去除已抢订单
						console.log("order==", order);
						let st = JSON.stringify(that.getArrEqual(allOid, order));
						// console.log("st", st);
						// console.log("==", that.getArrEqual(allOid, order));
						// if (that.getArrEqual(allOid, order).length != 0) {
						// 	clearInterval(set);
						// 	uni.navigateTo({
						// 		url: '/pages/competed/index?st=' + st
						// 	})
						// }
					})
				})
			}, 3000)
		})
		//审核中
		that.$http.findOrderMsgs({
			openId: openId,
			orderStart: 3
		}).then(res => {
			that.notStart = res.data.data
		})
	},
	computed: {
		isModel: function() {
			//console.log(this.$store.state.model)
			return this.$store.state.model;
		},
		isHeight: function() {
			return this.$store.state.windowHeight
		}
	},
	methods: {
		// 视频进入全屏显示
		findDetailUserMsg(e) {
			console.log("video=",e);
			console.log("123");
		},
		// 去除已抢订单
		getArrEqual(arr1, arr2) {
			for (let i = 0; i < arr2.length; i++) {
				for (let j = 0; j < arr1.length; j++) {
					if(arr2[i].oid == arr1[j]) {
						arr2.splice(i, 1)
					}
				}
			}
			return arr2;
		},
		// 请求事件
		isRequest() {
			return new Promise((resolve, reject) => {
				setTimeout(() => {
					uni.showToast({
						icon: 'success',
						title: '请求数据成功'
					})
					resolve()
				}, 1000)
			})
		}, 
		// 导航栏点击
		navClick(index) {
			this.currentTab = index //设置swiper的第几页
			this.tabClick = index //设置导航点击了哪一个
			this.navIndex = index;
			let _this = this;
			let openId = uni.getStorageSync('openid');

			if (index == 1) {
				//进行中
				_this.$http.findOrderMsgs({
					openId: openId,
					orderStart: 2
				}).then(res => {
					_this.start = res.data.data
				})
			} else if (index == 2) {
				//已完成
				_this.$http.findOrderMsgs({
					openId: openId,
					orderStart: 4
				}).then(res => {
					_this.end = res.data.data
				})

			} else if (index == 0) {
				//审核中
				_this.$http.findOrderMsgs({
					openId: openId,
					orderStart: 3
				}).then(res => {
					_this.notStart = res.data.data
				})
			}
		},
		// swiper 滑动
		swiperTab: function(e) {
			//console.log(e)
			var index = e.detail.current //获取索引
			this.navClick(index)
		},
		lower1() {
			//console.log(`加载${this.currentTab}`)
			uni.showLoading({
				content: '加载中'
			})
			setTimeout(() => {
				uni.hideLoading()
			}, 1000)
		},
		upHead: function(e) {
			this.$store.commit('setQiangSt', true);
			uni.navigateTo({
				url: '/pages/upHead/index?pic=' + e
			})
		},
		upAimg: function() {
			let _this = this;
			//console.log(123)
			this.$store.commit('setQiangSt', true);
			uni.chooseImage({
				count: 3,
				success: (chooseImageRes) => {
					console.log(chooseImageRes)
					let tempFilePaths = chooseImageRes.tempFilePaths;
					_this.aimg = tempFilePaths
				}
			});
		},
		upLvli: function(e) {
			let _this = this;
			let openId = uni.getStorageSync('openid');
			// console.log(e.detail.value)
			e.detail.value.activityBeginTime = this.date;
			e.detail.value.openId = openId;

			if (e.detail.value.activityBeginTime == null || this.date == '' || this.aimg == '') {
				uni.showModal({
					title: '提示',
					content: '请填写完整信息'
				})
				return false
			}
			
			uni.request({
				url: _this.$store.state.baseurl + '/record/create',
				data: e.detail.value,
				method: 'POST',
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: function(res) {
					var uuid = res.data.data
					for (var i = 0; i <= _this.aimg.length; i++) {
						uni.uploadFile({
							url: _this.$store.state.baseurl + '/record/fileUpload',
							filePath: _this.aimg[i],
							name: 'file',
							formData: {
								uuid: uuid,
								openId: e.detail.value.openId
							},
							success: (res) => {
								console.log(res)
								_this.$store.commit('setQiangSt', false)
								setTimeout(function() {
									_this.uplv = false
									uni.reLaunch({
										url: '/pages/index01/index'
									})
								}, 1000)
							},
							fail() {
								uni.showToast({
									title: '上传失败'
								})
							}
						});
					}
					_this.notStart = res.data.data
					//console.log( _this.notStart)
				}
			})
		},
		bindDateChange: function(e) {
			this.date = e.target.value
		},
		showlv: function() {
			uni.navigateTo({
				url: '/pages/uploadCareer/uploadCareer'
			})
		},
		showMessage: function() {
			uni.navigateTo({
				url: '/pages/userInfo/userInfo'
			})
		},
		closeLvli: function() {
			this.uplv = false;
			this.$store.commit('setQiangSt', false)
			let upLv = this.$store.getters.isSt;
		},
		callMe: function() {
			uni.makePhoneCall({
				phoneNumber: '18628188261'
			});
		},
		orderMsg: function(oid) {
			uni.navigateTo({
				url: '/pages/staffOrderMsg/index?oid=' + oid
			})
		},
		// 预览图片
		preivewImgs(res) {
			console.log("res", res);
			let Imgsindex = res.currentTarget.id;  
			uni.previewImage({
				urls:this.picsList,
				current:Imgsindex
			})
		}
	}
}
